@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "iconfont";
  src: url("fonts/iconfont.eot");
  src: url("fonts/iconfont.eot?#iefix") format("eot"), url("fonts/iconfont.woff2") format("woff2"), url("fonts/iconfont.woff") format("woff"), url("fonts/iconfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  display: inline-block;
}

.icon-facebook:before {
  content: "Ó®Å";
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
}

body .container {
  width: 100%;
  max-width: 1250px;
  padding: 0 10px;
  margin: 0 auto;
}

.order {
  background-color: #f9f7ee;
}

.order__wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 15px;
  padding-bottom: 92px;
}

@media only screen and (max-width: 1023px) {
  .order__wrap {
    flex-direction: column;
  }
}

.order__title {
  padding-top: 30px;
  font-size: 32px;
  font-family: sans-serif, "OpenSans";
  color: #231f20;
  font-weight: normal;
}

.order__info {
  display: inline-block;
  padding-top: 25px;
  padding-bottom: 15px;
  font-size: 18px;
  font-family: sans-serif, "Arial";
  color: #231f20;
  font-weight: bold;
}

.order__label {
  display: inline-block;
  width: 17.5%;
  margin-right: 5px;
  font-size: 16px;
  font-family: sans-serif, "Arial";
  color: #8d8d8d;
  text-align: left;
}

.order__input {
  display: inline-block;
  width: 45%;
  margin: 10px 0;
  padding: 5px 10px;
  border-style: solid;
  border-width: 1px;
  border-color: #acacac;
  border-radius: 3px;
  background-color: white;
  box-shadow: inset 0 1px 4px 0 rgba(0, 0, 0, 0.15);
  font-size: 16px;
  font-family: sans-serif, "Arial";
  color: #000;
}

.order__input:focus {
  border-color: #57b816;
  box-shadow: 0 0 5px 0 #3f4092;
}

.order__comment {
  display: inline-block;
  margin-left: 18.7%;
  margin-top: 12px;
  font-size: 16px;
  font-family: sans-serif, "Arial";
  color: #0072bc;
}

.order__comment:hover, .order__comment:active {
  color: #004B7C;
  transition: color .3s ease-in;
}

.order__submit {
  display: inline-block;
  margin-left: 18.5%;
  padding: 22px 0;
}

.order__submit button {
  padding: 8px 17px 6px;
  font-size: 18px;
  font-family: sans-serif, "Arial";
  color: #fff;
  font-weight: bold;
  background-color: #57b816;
  border-radius: 30px;
  transition: .3s ease-out;
}

.order__submit button:hover, .order__submit button:active {
  background-color: #69D91E;
  box-shadow: 0 0 10px 0 #161e0f;
}

.order__list {
  display: inline-block;
  padding: 10px;
  width: 32.5%;
  height: fit-content;
  border: solid 1px #e0dfca;
  border-radius: 4px;
  background-color: #fff;
}

@media only screen and (max-width: 1023px) {
  .order__list {
    width: 100%;
    margin-top: 20px;
  }
}

.order__list__title {
  padding: 20px 10px 0;
  font-size: 18px;
  font-family: sans-serif, "Arial";
  color: #231f20;
  font-weight: bold;
}

.order__list__title:after {
  content: '';
  display: block;
  margin: 0 -20px;
  padding-bottom: 20px;
  border-bottom: dotted 1px #E0DFCA;
}

.order__item {
  padding: 15px 5px;
}

.order__item:after {
  content: " ";
  display: table;
  clear: both;
}

.order__item:after {
  content: '';
  display: block !important;
  margin: 0 -15px;
  padding-bottom: 20px;
  border-bottom: dotted 1px #E0DFCA;
}

.order__item__img {
  display: inline-block;
  width: 20%;
  margin-right: 15px;
  padding: 0 5px;
  float: left;
}

.order__item__desc {
  display: inline-block;
  width: 75%;
  margin-bottom: 15px;
}

.order__item__desc a {
  font-size: 14px;
  font-family: sans-serif, "Arial";
  color: #0072bc;
  text-decoration: underline;
}

.order__item__desc a:hover, .order__item__desc a:active {
  color: #3e4baf;
  transition: color .2s ease;
}

.order__item__amount {
  display: inline-block;
  font-size: 14px;
  font-family: sans-serif, "Arial";
  color: rgba(0, 0, 0, 0.6);
}

.order__item__total {
  display: inline-block;
  float: right;
  font-size: 14px;
  font-family: sans-serif, "Arial";
  color: black;
  font-weight: bold;
}

.order__sum {
  display: inline-block;
  float: right;
  padding: 15px 10px;
  font-size: 18px;
  font-family: sans-serif, "Arial";
  color: black;
  font-weight: bold;
}

.customer {
  display: inline-block;
  width: 65%;
}

@media only screen and (max-width: 1023px) {
  .customer {
    width: 100%;
  }
}

.customer__tabs {
  display: flex;
  justify-content: flex-start;
  margin-bottom: -1px;
}

.customer__tabs li {
  display: inline-block;
  padding: 15px 12px;
  box-sizing: border-box;
  font-size: 14px;
  font-family: sans-serif, "Arial";
  font-weight: bold;
  line-height: 1.286;
  text-align: left;
}

.customer__tabs li:first-child {
  width: 120px;
  border-left: solid 1px #E0DFCA;
  border-right: solid 1px #E0DFCA;
  border-top: solid 1px #E0DFCA;
  border-top-left-radius: 4px;
}

.customer__tabs li:nth-child(2) {
  width: 200px;
  border-top: solid 1px #E0DFCA;
  border-right: solid 1px #E0DFCA;
  border-bottom: solid 1px #E0DFCA;
  border-top-right-radius: 4px;
}

.customer__tabs li:hover {
  cursor: pointer;
}

.customer__tabs .active {
  color: black;
  background-color: #ffffff;
}

.customer__tabs .inactive {
  color: #2073b3;
  text-decoration: underline;
  text-decoration-style: dotted;
  text-decoration-color: #2073b3;
}

.customer__comment {
  width: 80%;
  margin-top: 10px;
  margin-left: 18.9%;
  padding: 10px;
  box-sizing: border-box;
  font-size: 16px;
  font-family: sans-serif, "Arial";
  color: #000;
}

.customer__comment:focus {
  border-color: #57b816;
  box-shadow: 0 0 5px 0 #3f4092;
}

.customer__wrap {
  padding-left: 29px;
  padding-right: 32px;
  background-color: #ffffff;
  border: solid 1px #E0DFCA;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.customer__wrap .delivery-select,
.customer__wrap .warehouse-select,
.customer__wrap .payment-select {
  display: inline-block;
  position: relative;
  padding: 5px;
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  border-color: #ACACAC;
  box-shadow: inset 0 1px 4px 0 rgba(0, 0, 0, 0.15);
  background-color: #fff;
  font-size: 16px;
  font-family: sans-serif, "Arial";
  color: #000;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  background: url("../img/arrow-down.png");
  background-repeat: no-repeat;
  background-position: right 10px;
}

.customer__wrap .delivery-select:focus,
.customer__wrap .warehouse-select:focus,
.customer__wrap .payment-select:focus {
  border-color: #57b816;
  box-shadow: 0 0 5px 0 #3f4092;
}

.customer__info:after, .customer__delivery:after, .customer__payment:after {
  content: '';
  display: block;
  margin: 0 -32px 0 -29px;
  padding-bottom: 20px;
  border-bottom: dotted 1px #E0DFCA;
}

.customer__info .is-hide {
  display: none;
}

.customer__info .is-visible {
  display: inline-block;
}

.customer__delivery .order__info {
  width: 17.5%;
  margin-right: 5px;
}

.customer__delivery .delivery-select {
  width: 45%;
  margin-bottom: 10px;
}

.customer__delivery .warehouse-select {
  width: 80%;
  margin-top: 10px;
}

.customer__payment .order__info {
  width: 17.5%;
  margin-right: 5px;
  padding-bottom: 5px;
}

.customer__payment .payment-select {
  width: 45%;
}
